import { Dropdown as BDropdown } from 'react-bootstrap';
import {useState} from 'react';

export default function Dropdown(props) {
    const [selectedValue, setSelectedValue] = useState("Price");

    return <BDropdown>
        <BDropdown.Toggle id="dropdown-basic">
            {selectedValue}
        </BDropdown.Toggle>
        <BDropdown.Menu>
            <BDropdown.Item href="#/action-1">Price</BDropdown.Item>
            <BDropdown.Item href="#/action-2">Time</BDropdown.Item>
            <BDropdown.Item href="#/action-3">Downtime</BDropdown.Item>
        </BDropdown.Menu>
    </BDropdown>
}